.contactPage {
   z-index: 100;
   display: flex;
   position: absolute;
   margin: auto;
   width: 100%;
   height: 100%;
   background-color: rgb(0, 1, 51);
   flex-direction: column;
}

.contactHeader {
   display: flex;
   justify-content: center;
   align-items: center;
   width: 50%;
   flex: 1;
   color: white;
   font-size: 4vh;
}

.contactWrapper {
   flex: 2;
   display: flex;
}

.contactForm {
   position: relative;
   flex: 1;
}

.contactForm form {
   position: absolute;
   width: 85%;
   height: 65%;
   top: 10%;
   left: 0;
   right: 0;
   margin: auto;
}

.contactForm p {
   font-size: 15px;
   width: 100%;
   height: 20%;
   color: red;
}

.contactForm .input {
   font-size: 1.4em;
   color: white;
   position: relative;
   padding-left: 2%;
   border: 0;
   background-color: rgb(0, 0, 75);
   width: 100%;
   height: 80%;
}

.formName {
   position: relative;
   padding: 5px;
   float: left;
   width: 50%;
   height: 80px;
   z-index: 1;
}

.input:focus {
   border-bottom: 2px solid rgb(0, 151, 113);
}

input:focus::placeholder {
   font-size: 0.8em;
}

.contactForm textarea:focus {
   border-bottom: 2px solid rgb(0, 151, 113);
}

.contactForm textarea:focus {
   font-size: 1.1em;
}

.formMail {
   padding: 5px;
   float: left;
   width: 50%;
   height: 80px;
   z-index: 1;
}

.formArea {
   padding: 5px;
   width: 100%;
   height: 80%;
   margin-top: 80px;
}

.formSubmit {
   color: white;
   border: rgb(0, 151, 113) 1px solid;
   background-color: transparent;
   cursor: pointer;
   width: 15%;
   height: 10%;
   position: absolute;
   bottom: 5%;
   right: 5px;
   margin: auto;
   transition: 0.3s ease;
   font-size: 1em;
}

.formSubmit:hover {
   background-color: rgb(0, 151, 113);
   color: rgb(0, 0, 75);
}

.contactForm .input:focus {
   outline: none;
}

.contactForm textarea {
   font-family: 'Ubuntu', sans-serif;
   font-size: 1.4em;
   color: white;
   padding: 1%;
   border: 0;
   width: 100%;
   height: 70%;
   background-color: rgb(0, 0, 75);
   resize: none;
}

.contactForm textarea:focus {
   outline: none;
}

::placeholder {
   color: rgb(175, 175, 175);
   transition: 0.1s ease;
}

.contactText {
   box-sizing: border-box;
   display: flex;
   align-items: center;
   flex-direction: column;
   padding-top: 5%;
   flex: 1;
   color: white;
}

.contactText h3 {
   font-size: 5vh;
   width: 80%;
}

.contactText a {
   margin-top: 2%;
   text-decoration: none;
   font-size: 5vh;
   width: 80%;
   color: rgb(206, 206, 206);
}

.contactText span {
   color: rgb(0, 151, 113);
}

.welcomeContact {
   flex-direction: column;
   position: relative;
   display: flex;
   width: 85%;
   height: 65%;
}

.welcomeContact div {
   color: white;
   display: flex;
   align-items: center;
   flex: 1;
}

.welcomeContact div h1,
h2 {
   padding-left: 5%;
   font-size: 7vh;
}

.welcomeContact h2 {
   color: rgb(0, 151, 113);
}

.welcomeContact h1 {
   padding-left: 5%;
}

.welcomeContact span {
   color: rgb(0, 151, 113);
   font-size: 7vh;
   opacity: 0;
   transition: 0.2s;
}

.welcomeContact:before {
   position: absolute;
   border-top: 5px solid rgb(0, 151, 113);
   border-bottom: 5px solid rgb(0, 151, 113);
   width: 0%;
   height: 100%;
   right: 0;
   left: 0;
   margin: auto;
   content: '';
   animation: border 1s forwards;
   animation-delay: 0.7s;
}

.rollContact::before {
   animation: roll 0.3s forwards;
}

@keyframes roll {
   0% {
      width: 100%;
   }

   100% {
      width: 0%;
   }
}

@keyframes border {
   0% {
      width: 0%;
   }

   100% {
      width: 100%;
   }
}

@media only screen and (max-width: 1000px) {
   .contactWrapper {
      flex-direction: column;
   }

   .contactHeader {
      width: 100%;
      justify-content: flex-start;
   }

   .welcomeContact div h1 {
      font-size: 6vh;
   }

   .welcomeContact div h2 {
      font-size: 6vh;
   }

   .welcomeContact {
      width: 100%;
   }

   .welcomeContact::before {
      border: 0;
   }

   .contactText {
      padding: 0;
   }

   .contactWrapper {
      flex: 2.2;
   }

   .contactText h3 {
      font-size: 3vh;
   }

   .contactText a {
      font-size: 3vh;
   }

   .contactForm {
      flex: 2;
   }

   .contactForm form {
      top: 0;
      height: 90%;
   }

   .contactForm .input {
      font-size: 1em;
   }

   .contactForm textarea {
      font-size: 1em;
   }

   .formArea {
      margin-top: 100px;
      height: 60%;
   }

   .formSubmit {
      width: 25%;
   }

   .formMail {
      width: 100%;
      height: 65px;
   }

   .formName {
      width: 100%;
      height: 65px;
   }
}
