.aboutPage {
   z-index: 100;
   display: flex;
   position: absolute;
   margin: auto;
   width: 100%;
   height: 100%;
   background-color: rgb(0, 1, 51);
   flex-direction: column;
}

.aboutHeader {
   visibility: hidden;
   flex-direction: row;
   display: flex;
   flex: 1;
}

.aboutTitle {
   align-items: center;
   justify-content: center;
   display: flex;
}

.welcome {
   flex-direction: column;
   position: relative;
   display: flex;
   width: 85%;
   height: 50%;
}

.welcome div {
   color: white;
   display: flex;
   /* overflow: hidden; */
   align-items: center;
   flex: 1;
}

.welcome div h1,
h2 {
   padding-left: 5%;
   font-size: 7vh;
}

.welcome h2 {
   color: rgb(0, 151, 113);
}

.welcome span {
   color: rgb(0, 151, 113);
   font-size: 7vh;
   opacity: 0;
   transition: 0.2s;
}

.app .active {
   opacity: 1;
}

.welcome:before {
   position: absolute;
   border-top: 5px solid rgb(0, 151, 113);
   border-bottom: 5px solid rgb(0, 151, 113);
   width: 0%;
   height: 100%;
   right: 0;
   left: 0;
   margin: auto;
   content: '';
   animation: border 1s forwards;
   animation-delay: 0.4s;
}

.rollWelcome::before {
   animation: roll 0.3s forwards;
}

@keyframes roll {
   0% {
      width: 100%;
   }

   100% {
      width: 0%;
   }
}

@keyframes border {
   0% {
      width: 0%;
   }

   100% {
      width: 100%;
   }
}

.aboutImage {
   display: flex;
   justify-content: center;
   align-items: center;
}

.imageSpace {
   align-items: center;
   justify-content: center;
   position: relative;
   display: flex;
   width: 45%;
   height: 90%;
   overflow: hidden;
}

.imageSpace img {
   overflow: hidden;
   border-radius: 30px;
   width: 100%;
   max-height: 100%;
}

.imageSpace::before {
   display: flex;
   align-items: center;
   justify-content: center;
   border-radius: 30px;
   position: absolute;
   background-color: rgba(0, 7, 70, 0.459);
   width: 0%;
   height: 0%;
   content: '';
   transition: 0.2s;
}

.imageSpace:hover::before {
   width: 100%;
   height: 100%;
}

.imageSpace:hover .imageText {
   opacity: 1;
}

.imageText {
   user-select: none;
   z-index: 0;
   transition: 0.2s;
   position: absolute;
   font-size: 4vh;
   opacity: 0;
   color: white;
   font-family: 'Indie Flower', cursive;
}

.aboutContent {
   visibility: hidden;
   position: relative;
   flex-direction: row;
   display: flex;
   flex: 1.5;
}

.aboutText {
   display: flex;
   align-items: center;
   justify-content: center;
   color: white;
}

.aboutFlex {
   flex: 1;
}

.aboutSkills {
   display: flex;
   flex-direction: column;
}

.textSpace {
   font-size: 4vh;
   width: 85%;
   height: 80%;
}

.textSpace p {
   margin-top: 3%;
}

.skills {
   overflow: hidden;
   height: 80%;
   width: 100%;
   display: flex;
   flex-direction: row;
   color: white;
   flex: 3;
}

.skillsTitle {
   display: flex;
   justify-content: center;
   align-items: center;
   font-size: 3vh;
   flex: 1;
   color: white;
}

.skills h2 {
   color: rgb(0, 7, 87);
   text-align: center;
   position: absolute;
   left: 20%;
   height: 100%;
   width: 50%;
   font-size: 10vh;
   font-family: 'Oswald', sans-serif;
   writing-mode: vertical-rl;
   text-orientation: mixed;
}

.skills div {
   width: 100%;
   position: relative;
}

.skills ul {
   position: relative;
   flex: 2;
   height: 100%;
   list-style: none;
   width: 80%;
   float: right;
}

.skills li {
   cursor: pointer;
   font-size: 4vh;
   display: flex;
   align-items: center;
   justify-content: center;
   height: 20%;
   width: 100%;
}

.skills li p {
   transition: 0.5s ease;
}

