.before:hover p {
   opacity: 0;
}

.skills li:hover::before {
   opacity: 1;
}

.skillsIcon {
   font-size: 5vh;
}

.spanBack {
   visibility: hidden;
   width: 10%;
   display: flex;
   height: 10%;
   cursor: pointer;
   position: absolute;
   justify-content: center;
   font-size: 4vh;
   font-weight: 700;
   bottom: 5%;
   right: 0;
   left: 0;
   margin: auto;
   letter-spacing: 0.1em;
   color: grey;
   transition: all 0.3s ease;
   text-transform: uppercase;
}

.spanBack::before {
   content: '';
   position: absolute;
   color: white;
   width: 100%;
   height: 5%;
   bottom: 0;
   right: 0;
   left: 0;
   margin: auto;
   background-color: rgb(7, 0, 109);
   overflow: hidden;
   z-index: -1;
   transition: all 0.3s ease;
}

.spanBack:hover::before {
   height: 100%;
}

.spanBack:hover {
   color: white;
}

/* M O B I L E */

@media only screen and (max-width: 1200px) {
   .aboutHeader {
      flex-direction: column;
   }

   .aboutTitle {
      padding-top: 10%;
      padding-bottom: 10%;
   }

   .aboutContent {
      flex-direction: column;
   }

   .aboutImage {
      flex-grow: 2;
   }

   .aboutPage {
      height: 200%;
   }

   .welcome:before {
      border: 0;
   }

   .welcome {
      width: 100%;
   }

   .welcome div h1 {
      font-size: 6vh;
   }

   .welcome div h2 {
      font-size: 6vh;
   }

   .aboutTitle {
      align-items: flex-start;
      justify-content: flex-start;
   }

   .textSpace {
      text-align: center;
   }
}

@media only screen and (max-width: 600px) {
   .imageSpace {
      width: 80%;
   }
}

@media only screen and (max-width: 1610px) {
   .welcome {
      height: 60%;
   }
}
