* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
}

body {
   font-family: 'Ubuntu', sans-serif;
   /* background-image: url("./images/bgc.jpg"); */
   background-position: center;
}

::-webkit-scrollbar {
   width: 8px;
}

::-webkit-scrollbar-thumb {
   background: rgb(0, 73, 55);
}

::-webkit-scrollbar-track {
   background: rgb(0, 1, 51);
}

.app {
   display: flex;
   overflow: hidden;
   width: 100%;
   height: 100vh;
   background-position: center;
   /* background: linear-gradient(
      180deg,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 0.8435749299719888) 0%
   ); */
}

.backIcon {
   color: #009771;
   position: fixed;
   font-size: 3.5em;
   bottom: 20px;
   right: 20px;
   /* border-radius: 40px; */
}
