.homePage {
   width: 100%;
   height: 100%;
}

.header {
   user-select: none;
   z-index: 100;
   position: absolute;
   top: 10%;
   display: flex;
   align-items: center;
   justify-content: center;
   flex-direction: column;
   right: 0;
   left: 0;
   margin: auto;
   width: 50vw;
   height: auto;
}

.header h1 {
   z-index: 200;
   text-align: center;
   font-family: 'Ubuntu', sans-serif;
   color: white;
   font-size: 7vh;
}

.header p {
   z-index: 100;
   font-size: 3vh;
   text-align: center;
   font-family: 'Ubuntu', sans-serif;
   color: rgb(0, 151, 113);
}

.main {
   position: relative;
   width: 100%;
   height: 70%;
}

.menu {
   z-index: 100;
   position: absolute;
   top: 50%;
   right: 0;
   left: 0;
   margin: auto;
   width: 45vh;
   height: 45vh;
}

.box1,
.box2 {
   overflow: hidden;
   position: absolute;
   width: 8vh;
   height: 8vh;
   background-color: rgb(165, 165, 165);
   left: 0;
   right: 0;
   top: 0;
   bottom: 0;
   margin: auto;
   opacity: 1;
}

.icon {
   visibility: hidden;
   opacity: 0;
   position: absolute;
   margin: auto;
   color: rgb(165, 165, 165);
   font-size: 6.5vh;
   transition: 0.2s ease;
   animation: show 1s forwards;
   animation-delay: 1.5s;
}

.icon:hover {
   color: white;
   cursor: pointer;
}

.icon1 {
   top: 10%;
   right: 0;
   left: 0;
}

.icon2 {
   top: 0;
   bottom: 0;
   left: 10%;
}

.icon3 {
   top: 0;
   bottom: 0;
   right: 10%;
}

.icon4 {
   bottom: 10%;
   right: 0;
   left: 0;
}

@media only screen and (max-width: 500px) {
   footer {
      font-size: 0.7em;
   }

   .menu {
      overflow: hidden;
   }
}
#tsparticles {
   position: fixed;
   background-color: rgb(0, 1, 51);
   width: 100vw;
   height: 100vh;
   z-index: 10;
}
.menuSpace {
   color: rgb(13, 0, 83);
   font-size: 2.3vh;
   text-align: center;
   display: flex;
   align-items: center;
   justify-content: center;
   z-index: 1;
   position: absolute;
   top: 0;
   right: 0;
   left: 0;
   bottom: 0;
   margin: auto;
   width: 8vh;
   height: 8vh;
   font-weight: bold;
}

.icon:hover .box1 {
   background-color: #fff;
}

.white {
   background-color: white;
   transition: all 0.2s ease;
}

footer {
   text-align: center;
   display: flex;
   justify-content: center;
   align-items: center;
   position: absolute;
   width: 100%;
   height: 5%;
   bottom: 0;
   color: rgb(128, 128, 128);
}

/* SOCIAL ICONS STYLES */
.socialWrapper {
   border-radius: 50px;
   overflow: hidden;
   display: flex;
   justify-content: center;
   align-items: center;
   width: 100%;
   height: 100%;
}

.socialWrapper a {
   cursor: pointer;
   margin-left: 5%;
   font-size: 7vh;
   color: rgb(75, 75, 75);
}

.backSocialIcon {
   cursor: pointer;
   display: flex;
   align-items: center;
   position: absolute;
   right: 0;
   top: 0;
   bottom: 0;
   margin: auto;
   font-size: 5.5vh;
   width: 10%;
   align-self: flex-end;
   color: rgb(0, 0, 0);
}

#socialBackIcon {
   z-index: 1;
   color: rgb(0, 3, 167);
}

.backSocialIcon::before {
   position: absolute;
   right: 0;
   width: 0%;
   height: 100%;
   content: '';
   transition: 0.4s ease;
   background-color: rgb(165, 165, 165);
}

.backSocialIcon:hover::before {
   width: 100%;
}
@media only screen and (max-width: 800px) {
   .header {
      width: 100vw;
   }
}
